export const composeSlug = params => {
  return params
    ? params
        .trim()
        .replace(/[^a-zA-Z ]/g, '')
        .replace(/ /g, '-')
    : ''
}

export const encodeForm = data => {
  return data
    ? Object.keys(data)
        .map(
          key => encodeURIComponent(key) + '=' + encodeURIComponent(data[key])
        )
        .join('&')
    : ''
}
